import request from "network/request.js"

//
export function requestupdatecontact(parameter) {
    return request({
        url: "/contact/updatecontact",
        method: 'post',
        params: parameter
    })
}

//
export function requestaddContact(parameter) {
    return request({
        url: "/contact/addContact",
        method: 'post',
        params: parameter
    })
}