import request from "network/request.js"

//
export function requestLogin(parameter) {
    return request({
        url: "/user/login",
        params: parameter
    })
}

// 
export function requestcheckuser(parameter) {
    return request({
        url: "/user/checkuser",
        params: parameter
    })
}

// 
export function requestregister(parameter) {
    return request({
        url: "/user/register",
        method: "post",
        params: parameter
    })
}